<template>
  <div>
    <div class="write">
      <v-flex>
        <v-text-field
        label="To Do를 작성해 주세요"
        v-model="writeTodo"
        style="float: left; width: 80%"
        @keyup.enter="postTodo"
        ></v-text-field>
      </v-flex>
    </div>
    <div class="clear" style="width: 100%">
      <span class="listId"><h2>to do</h2></span>
      <span class="listId"><h2>progress</h2></span>
      <span class="listId"><h2>complete</h2></span>
    </div>
    <span v-for="(list, i) in lists" :key="i" class="clear">
      <drop class="drop list bord" @drop="handleDrop(i, list, ...arguments)">
        <drag v-for="item in list"
        @drag = "setDrag(i)"
         class="drag card cardstyle"
        :key="item.id"
        :class="{ [item]: true }"
        :transfer-data="{ item: item, list: list, example: 'lists' }">
        <div class="right discard" @click="discard(item.id)"><i class="fas fa-times"></i></div>
        {{ item.body }}
      </drag>
    </drop>
  </span>
  <div class="clear"></div>
</div>
</template>

<script>
import { Drag, Drop } from "vue-drag-drop";
import FirebaseService from "@/services/FirebaseService";

export default {
  components: { Drag, Drop },
  data() {
    return {
      writeTodo: "",
      drag: -1,
      lists: [
        [],
        [],
        []
      ]
    };
  },
  mounted() {
    this.getTodo();
    this.getProgress();
    this.getComplete();
  },
  methods: {
    setDrag(index) {
      this.drag = index;
    },
    discard(uid) {
      FirebaseService.discardinAll(uid).then(r => {
        window.location.reload();
      });
    },
    postTodo(uid) {
      FirebaseService.postTodo(this.writeTodo, uid).then(r => {
        window.location.reload();
      });
    },
    getTodo() {
      let todo = FirebaseService.getTodo();
      if (this.lists[0].length != 0) {
        this.lists[0] = [];
      }
      todo.then(r => {
        for (var i = 0; i < r.length; i++) {
          if (r[i].dataMap.user == this.$store.state.user.uid) {
            var idandbody = { id: r[i].id, body: r[i].dataMap.body };
            this.lists[0].push(idandbody);
          }
        }
      });
    },
    getProgress() {
      let todo = FirebaseService.getProgress();
      todo.then(r => {
        for (var i = 0; i < r.length; i++) {
          if (r[i].dataMap.user == this.$store.state.user.uid) {
            var idandbody = { id: r[i].id, body: r[i].dataMap.body };
            this.lists[1].push(idandbody);
          }
        }
      });
    },
    getComplete() {
      let todo = FirebaseService.getComplete();
      todo.then(r => {
        for (var i = 0; i < r.length; i++) {
          if (r[i].dataMap.user == this.$store.state.user.uid) {
            var idandbody = { id: r[i].id, body: r[i].dataMap.body };
            this.lists[2].push(idandbody);
          }
        }
      });
    },
    handleDrop(index, toList, data) {
      const fromList = data.list;
      if (fromList) {
        toList.push(data.item);
        fromList.splice(fromList.indexOf(data.item), 1);
        toList.sort((a, b) => a > b);
      }
      this.writeTodo = data.item.body;
      var card = [];
      console.log("drag : " + this.drag);
      console.log("drop : " + index);
      if (this.drag == 0) {
        FirebaseService.getOneTodo(data.item.id).then(r => {
          card = r;
        });
        // FirebaseService.discardTodo(data.item.id);
      } else if (this.drag == 1) {
        card = FirebaseService.getOneProgress(data.item.id);
        FirebaseService.discardProgress(data.item.id);
      } else if (this.drag == 2) {
        FirebaseService.discardComplete(data.item.id);
      }
    }
  }
};
</script>

<style scoped>
.drag {
  display: inline-block;
}
.drop {
  display: inline-block;
  vertical-align: top;
  padding: 15px;
  margin-bottom: 20px;
  width: auto;
  height: auto;
}
.listId {
  padding: 10px;
  margin: 20px;
  width: 30%;
  text-align: center;
  float: left;
  color: #3d3d3d;
  font-family: 'Indie Flower', cursive;
}
.clear {
  clear: both;
}
.bord {
  margin-bottom: 1px;
  width: 30%;
  height: 100%;
  padding: 10px;
  margin: 20px;
}
.card {
  width:90%;
}
.right {
  float: right;
}
.write {
  margin: 50px;
  margin-top: 70px;
  width: 60%;
}
.discard {
  padding-right: 10px;
  color: white;
}
.cardstyle {
  background: #cfcfcf;
  color: black;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 16px;
}
</style>



